<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>Kelola Pengguna</h5>
				<small>Kelola Pengguna - <b>Peran</b></small>
			</div>

			<div class="card">
				<div class="card-body">
					<div class="text-right">
						<button @click="showAdd" class="btn btn-primary">Tambah Peran</button>
					</div>
					<hr>
					<div id="table-wrapper" class="mt-2">
						<table class="table table-bordered">
							<thead class="text-center">
								<tr>
									<th>ID</th>
									<th>Peran</th>
									<th>Hak Akses</th>
									<th>Tindakan</th>
								</tr>
							</thead>
							<tbody>
								<tr>
									<td><b>ID-0001</b></td>
									<td>Role 1</td>
									<td>
										<span class="badge badge-primary">Baca Pengguna</span> <span class="badge badge-success">Tambah Pengguna</span> <span class="badge badge-warning">Edit Pengguna</span> <span class="badge badge-danger">Hapus Pengguna</span>
									</td>
									<td>
										<button @click="showEdit" class="btn btn-sm btn-warning mr-1"><i class="fas fa-edit"></i></button>
										<button @click="doDelete"  class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
									</td>
								</tr>
							</tbody>
						</table>
					</div>

				</div>
			</div>
		</div>

		
		<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="alokasiTitle" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Tambah Peran</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">

						<div class="form-group">
							<label>Nama Peran</label>
							<input type="text" class="form-control" v-model="model.name">
						</div>

					</div>

					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Tutup</button>
						<button type="button" @click="doAdd" class="btn btn-success">Simpan</button>
					</div>
				</div>
			</div>
		</div>

		<div class="modal fade" id="modalAction" tabindex="-1" role="dialog" aria-labelledby="alokasiTitle" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Kelola Hak Akses</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">

						<div style="max-height: 200px; overflow-y: auto;">
							<table class="table table-bordered">
								<thead>
									<th>Hak Akses</th>
									<th>Tambah</th>
									<th>Baca</th>
									<th>Edit</th>
									<th>Hapus</th>
								</thead>
								<tbody>
									<tr v-for="(v,i) in permissions" :key="i">
										<td>{{ v.table }}</td>
										<td>
											<input type="checkbox" v-model="v.create">
										</td>
										<td>
											<input type="checkbox" v-model="v.read">
										</td>
										<td>
											<input type="checkbox" v-model="v.update">
										</td>
										<td>
											<input type="checkbox" v-model="v.delete">
										</td>
									</tr>
								</tbody>
							</table>
						</div>

					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Tutup</button>
						<button type="button" @click="doEdit" class="btn btn-primary">Simpan</button>
					</div>
				</div>
			</div>
		</div>

	</Dashboard>
</template>

<script>
	export default{
		name: 'Role',
		data(){
			return {
				datas: [],
				permissions: [],
				model: {
					name: null
				}
			}
		},
		mounted(){
			this.getPermissions();
		},
		methods: {
			getPermissions(){
				this.permissions.push({
					table: 'User',
					create: false,
					read: true,
					update: false,
					delete: true,
				});
				this.permissions.push({
					table: 'Attribute',
					create: true,
					read: false,
					update: false,
					delete: true,
				});
				this.permissions.push({
					table: 'Attribute Value',
					create: false,
					read: true,
					update: false,
					delete: true,
				});
				this.permissions.push({
					table: 'Field Type',
					create: true,
					read: false,
					update: true,
					delete: true,
				});
			},
			showAdd(){
				$("#modalAdd").modal('show');
			},
			showEdit(){
				$("#modalAction").modal('show');
			},
			doAdd(){
				$("#modalAdd").modal('hide');
				this.$toast.open({
					message: 'Peran Berhasil Disimpan !',
					type: 'info',
					position: 'top-right',
				});
			},
			doEdit(){
				$("#modalAction").modal('hide');
				this.$toast.open({
					message: 'Peran Berhasil Disimpan !',
					type: 'info',
					position: 'top-right',
				});
			},
			doDelete(){
				this.$toast.open({
					message: 'Peran Berhasil Dihapus !',
					type: 'info',
					position: 'top-right',
				});
			},
		}
	}
</script>